
html {
  font-size: 85px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  font: 400 16px/1.5 Arial, "Microsoft YaHei", sans-serif;
  margin: 0 auto;
  max-width: 640px;
  min-width: 320px;
}
ul,
li {
  list-style: none;
}
img {
  vertical-align: middle;
  border: none;
}
p {
  margin: 0;
  padding: 0;
}
button,
input {
  -webkit-appearance: none;
  outline: 0;
  border: none;
  border-radius: 0;
  background: 0 0;
}
.pop-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9;
}
.swiper-pagination-bullet {
  background: #000 !important;
}
.swiper-pagination-bullet-active {
  background: #333 !important;
}
.notransform > .swiper-wrapper {
  -webkit-transform: none !important;
  transform: none !important;
}
.qq-face-gif {
  width: 24px;
  height: 24px;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cooperation-html {
  padding: 0.1rem 0;
}
.cooperation-html p {
  margin: 0.2rem 0.1rem;
  font-size: 0.15rem;
}
.cooperation-html img {
  width: 100%;
}



